html body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: "Helvetica Neue", sans-serif;
}

.aboutContainer p{
  font-size: 2vw;
  margin-left: 0.5em;
  margin-top: 2vh;
}

a {
  text-decoration: none;
  margin-left: 2vh;
  color: black;
  -webkit-transition: color 0.27s;
  transition: color 0.27s;
}

a:hover{
  text-decoration: none;
  color: darkgrey;
}

form{
  width: 100%;
}

input {
    width: 100%;
    line-height: 30px;
    display: block;
    font-weight: 100;
    font-size: 2vw;
    position: relative;
    z-index: 2;
    -webkit-appearance: none;
    height: 100%;
    margin: 0 !important;
    border: 0;
    border-bottom: 1px solid #d8d8d8;
    outline: 0;
    text-align: center;
}

input[type="text"]{
    margin-top: 0;
    font-weight: 100;
    font-size: 2vw;
    letter-spacing: 0.7px;
}
.main{
  width: 100%;
  margin: 2vh;
}

.main img{
  width: 50%;
  margin-left: 0.5em;
  margin-top: 2vh;
}

.cardImg {
  width: 40%;
  transition: transform .3s;
}

.navigation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
  width: 100%;
  letter-spacing: 0.7px;
  margin: 0 !important;
  height: 3rem;
  line-height: 3rem;
  font-size: 1.5vw;
  vertical-align: middle;
}

button {
  display: none;
}

select{
  width: 100%;
  height: 3em;
  border: 0px;
  border-bottom: 1px solid #d8d8d8;
  text-align: center;
  text-align-last: center;
  font-size: 1vw;
  font-weight: 300;
  letter-spacing: 0.7px;
}

*:focus {
  outline: 0;
}

.selectBox{
  text-align: center;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: auto;
  margin-top: 3vh;
  margin: 3vh 3vh;
  margin-right: 3vh;
}

.card-container{
  display: flex;
  align-content: center;
  margin-top: 1vh;
  margin-bottom: 13vh;
}

.desc{
  margin-left: 1.5vw;
  width: 70%;
}

.title{
  font-size: 1.1vw;
  letter-spacing: 0.7px;
  font-weight: 350;
}

.author{
  font-size: 1vw;
  font-weight: 300;
  letter-spacing: 0.7px;
}

.published{
  font-size: 1vw;
  font-weight: 300;
}